.creditcardCard {
	height:400px;
}

.creditcardCard {
  > ul > li {
    padding-top: 5px;
    padding-bottom: 5px;
    cursor: pointer;
    &:hover {
      background: #cacaca;
    }
  }
  overflow-x: hidden;
}

.creditcardCard::-webkit-scrollbar-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #F5F5F5;
}

.creditcardCard::-webkit-scrollbar {
  width: 12px;
  background-color: #F5F5F5;
}

.creditcardCard::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #777777;
}